// @use 'abstracts/functions' as *;
@use 'abstracts/utility' as *;
@use 'abstracts/variables' as *;
@use 'abstracts/media' as *;


/*-----------------------------------------------------------------------------------*/
/* STYLES */
/*-----------------------------------------------------------------------------------*/

.pagination {
  text-align: center;
  padding-top: var(--space-heading);

  &__list {
    list-style: none;
    margin: 0;
    padding: 0;
  }

  &__item {
    display: inline-block;
    padding: 0 0.5rem;

    &::before {
      display: none;
    }

    a, span {
      margin: 0;
      display: block;
      font-weight: 400;
      font-size: 1.4rem;
      width: 3.5rem;
      height: 3.5rem;
      line-height: 3.2rem;
      text-indent: 0.2rem;
      padding: 0;
      color: var(--black);
      background: var(--white);
      border-width: 0.2rem;
      border-style: solid;
      border-color: var(--white);
      border-radius: 0.8rem;

      @include hover-focus {
        color: var(--white);
        background: var(--grey-dark);
        border-color: var(--grey-dark);
      }

      @include media-up(md) {
        font-size: 1.6rem;
        width: 5.5rem;
        height: 5.5rem;
        line-height: 5.2rem;
      }

      &.prev,
      &.next {
        padding: 0 3rem;
        width: auto;
        text-transform: uppercase;
      }

    }

    &--active {

      a, span { 
        cursor: not-allowed;
        color: var(--white);
        background: var(--accent);
        border-color: var(--accent);
      }

    }

  }

}

